import React from 'react';
import { Line } from'react-chartjs-2'

const China = () =>{

  return(
    <div>
        <h3>중국 감염자 현황!</h3>

        <div className="Pie">
            <Line
            
                data={{
                    options:{font:{
                        size:32
                    }},
                    labels: ['베이징', '광저우', '하얼빈', '상하이', '청두', '난닝'],
                    datasets: [{
                        label: '# of Votes',
                        data: [1323, 1131, 3323, 512, 1452, 643],
                        
                        
                        backgroundColor: [
                            'red',
                            'rgba(54, 162, 235, 0.2)',
                            'rgba(255, 206, 86, 0.2)',
                            'rgba(75, 192, 192, 0.2)',
                            'rgba(153, 102, 255, 0.2)',
                            'rgba(255, 159, 64, 0.2)'
                        ],
                        borderColor: [
                            'rgba(1, 99, 132, 1)',
                            'rgba(54, 162, 235, 1)',
                            'rgba(255, 206, 86, 1)',
                            'rgba(75, 192, 192, 1)',
                            'rgba(153, 102, 255, 1)',
                            'rgba(255, 159, 64, 1)'
                        ],
                        borderWidth: 10,
                    },
                
                ]

                }}
                height={1400}
                width={1600}

            />
        </div>
        
        

   

    </div>
  )
}

export default China