import React from 'react'
import { Link } from 'react-router-dom';



const Header = () => {
    return (
        <header className="header">
          <h1>Covid-19</h1>
          <ul>
            <Link to="/Korea">
              <li>한국</li>
            </Link>
            <Link to="/China">
              <li>중국</li>
            </Link>
          </ul>
        </header>
    )
}

export default Header
