import React from 'react';
import { Pie } from'react-chartjs-2'

const Korea = () =>{

  return(
    <div className="Korea1">
        <h3>한국 1일 감염자 현황</h3>

        <div className="Pie">
            <Pie
            
                data={{
                    options:{font:{
                        size:32
                    }},
                    labels: ['서울', '대전', '대구', '부산', '경기도', '일산'],
                    datasets: [{
                        label: '# of Votes',
                        data: [112, 52, 344, 225, 322, 31],
                        
                        
                        backgroundColor: [
                            'red',
                            'rgba(54, 162, 235, 0.2)',
                            'rgba(255, 206, 86, 0.2)',
                            'rgba(75, 192, 192, 0.2)',
                            'rgba(153, 102, 255, 0.2)',
                            'rgba(255, 159, 64, 0.2)'
                        ],
                        borderColor: [
                            'rgba(1, 99, 132, 1)',
                            'rgba(54, 162, 235, 1)',
                            'rgba(255, 206, 86, 1)',
                            'rgba(75, 192, 192, 1)',
                            'rgba(153, 102, 255, 1)',
                            'rgba(255, 159, 64, 1)'
                        ],
                        borderWidth: 10,
                    },
                ]

                }}
                height={1400}
                width={1600}

            />
        </div>

   

    </div>
  )
}

export default Korea